import React, { useState, useEffect } from 'react';
import { Container } from '@mui/material';
import { v4 as uuidv4 } from 'uuid';
import { MessageDto } from '../models/MessageDto';
import ChatInput from './ChatInput';
import MessageList from './MessageList';

const Chat = () => {
  const [isWaiting, setIsWaiting] = useState<boolean>(false);
  const [messages, setMessages] = useState<Array<MessageDto>>([]);
  const [input, setInput] = useState<string>("");

  const apiEndpoint = "https://your-api-placeholder.com";

  useEffect(() => {
    // When initializing the welcome message, specify 'text' as the type
    setMessages([new MessageDto(uuidv4(), false, "How can I help you today?", 'text')]);
  }, []);

  const handleSendMessage = async () => {
    if (input.trim() !== "") {
      // Include 'text' as the type for new messages
      const newMessage = new MessageDto(uuidv4(), true, input, 'text');
      setMessages(prevMessages => [...prevMessages, newMessage]);
      setInput(""); // Clear the input after sending the message

      try {
        await fetch(`${apiEndpoint}/sendMessage`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ message: input }),
        });
      } catch (error) {
        console.error("Failed to send message:", error);
      }
    }
  };

  const sendAudioChunk = async (audioBlob: Blob) => {
    try {
      await fetch(`${apiEndpoint}/sendAudioChunk`, {
        method: "POST",
        body: audioBlob,
        headers: {
          "Content-Type": "audio/wav",
        },
      });

      // Assuming the transcription result would be sent back here or through another mechanism
      // and you would add it to messages as a 'transcription' type
      // For now, this is left as an exercise for integration with your backend logic

    } catch (error) {
      console.error("Failed to send audio chunk:", error);
    }
  };

  const onSendEmail = async () => {
    const messageContent = messages.map(msg => msg.content).join("\n");
    try {
      await fetch(`${apiEndpoint}/sendEmail`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ content: messageContent }),
      });
      console.log("Email sent successfully.");
    } catch (error) {
      console.error("Failed to send email:", error);
    }
  };

  return (
    <Container>
      <MessageList messages={messages} />
      <ChatInput
        input={input}
        isWaiting={isWaiting}
        onInputChange={(e) => setInput(e.target.value)}
        onSendMessage={handleSendMessage}
        sendAudioChunk={sendAudioChunk}
        onSendEmail={onSendEmail}
      />
    </Container>
  );
};

export default Chat;
